body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pomodoro {
  width: 600px;
  height: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pomodoro-legend {
  color: white;
  font-size: 2em;
}

.pomodoro-separator {
  width: 100%;
  margin-bottom: 15px;
}

.timers {
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin-bottom: 20px;
}

.timer-config {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.timer-config-legend {
  color: white;
  padding: 5px;
}

.timer-config-time {
  background-color: white;
  color: #96211e;
  border-radius: 50%;
  padding: 6px;
  font-weight: bolder;
  margin: 5px;
  min-width: 18px;
  text-align: center;
}

.timer-config-button {
  border: 1px solid #bf3633;
  border-radius: 4px;
  padding: 0 12px 2px 12px;
  color: white;
  background-color: #96211e;
  font-size: 1.2em;
  margin: 5px;
}
.timer {
  border: 2px solid #bf3633;
  padding: 40px;
  border-radius: 50%;
  width: 300px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.timer-legend {
  color: white;
  font-size: 4em;
}

.cancel-btn, .resume-btn {
  border: 1px solid #bf3633;
  border-radius: 4px;
  padding: 20px;
  width: 200px;
  color: white;
  background-color: #96211e;
  font-size: 2em;
  margin: 10px;
}

.warning {
  background-color: #f4bc02;
}

.warning .timer-legend {
  color: #96211e;
}
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #96211e;
}

